html,
body {
  color: white;
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  white-space: nowrap;
  background-color: #1c1e23;
}

*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;

  ::-webkit-scrollbar {
    height: 0;
  }
}
